/* Lp overlay - to lp ph m  pha sau modal */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); /* Lp ph mu en m */
    display: flex;
    justify-content: center; /* Cn gia theo chiu ngang */
    align-items: center; /* Cn gia theo chiu dc */
    z-index: 9999; /* m bo modal hin th trn cc phn t khc */
}

/* Lp modal - cha ni dung ca modal */
.modal {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Thm bng m cho modal */
}

/* Phn loading text */
.loading {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    animation: pulse 1.5s infinite; /* Thm hiu ng nhp nhy */
}

/* Animation  to hiu ng nhp nhy cho ch "ang x l..." */
@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
}
